<template>
<div class="salesReturn_box">
    <div class="salesReturn_box_message">
        <img/>
        <div class="salesReturn_box_name">
            <p>嗷嗷叫</p>
            <span>1654823</span>
        </div>
    </div>
    <div class="salesReturn_box_text">
        <textarea v-model="abc"></textarea>
    </div>
    <div class="salesReturn_box_">
        <div class="salesReturn_box_img">
            <div class="salesReturn_box_un">
                <img class="salesReturn_box_un_img"/>
                <div class="salesReturn_box_del">
                    <img src="../../imgs/icon/del.png"/>
                </div>
            </div>
            <div class="salesReturn_box_load">
                <img src="../../imgs/icon/upload-pic.png"/>
            </div>
        </div>
        
    </div>
    <div class="salesReturn_box_button">提交</div>
</div>
</template>
<script>
    export default{
        data(){
            return {
                abc:""
            }
        },
        watch:{
            'abc':function(v){
                console.log(v)
            }
        }
    }

</script>
<style>
.salesReturn_box_message{
    display:flex;
    flex-direction:row;
    padding-left:.4rem;
    padding-right:.4rem;
    margin-top:.2rem;
    padding-top:.2rem;
    padding-bottom:.2rem;
    background-color:white;

}
.salesReturn_box_message>img{
    width:1.5rem;
    height:1.5rem;
    background:red;
}
.salesReturn_box_name{
    display:flex;
    flex-direction:column;
    margin-left:.2rem;
}
.salesReturn_box_name>p{
    font-size:.3rem;
    vertical-align:text-top;
}
.salesReturn_box_name>span{
    color:gray;
    font-size:.3rem;
    margin-top:.7rem;
}
.salesReturn_box_text{
    width:100;
    padding:0 .4rem;
    box-sizing:border-box;
}
.salesReturn_box_text>textarea{
    font-size:.3rem;
    padding:.2rem ;
    border:1px solid gainsboro;
    height:5rem;
    margin-top:.5rem;
    background-color:white;
    width:100%;
     box-sizing:border-box;
}
.salesReturn_box_{
    padding-top:20rpx;
    padding-left:40rpx;
    padding-right:40rpx;
    padding-bottom:200rpx;
    background-color:#f8f8f8;
}
.salesReturn_box_button{
    background-color:#e01a1a;
    display:flex;
    justify-content:center;
    align-items:center;
    color:white;
    font-size:.35rem;
    font-weight:bold;
    position:fixed;
    left:.5rem;
    right:.5rem;
    bottom:.5rem;
    height:1rem;
    line-height:1rem;
    vertical-align:middle;
}
.salesReturn_box_{

}
.salesReturn_box_img{
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:flex-start;
    padding-top:.2rem;
    padding-left:.4rem;
    padding-right:.4rem;
    padding-bottom:2rem;
    background-color:#f8f8f8;

}
.salesReturn_box_load{
    display:flex;
    flex-direction:row;
    align-items:center;
}
.salesReturn_box_load>img{
    width:1.5rem;
    height:1.5rem;
    margin-right:.2rem;
    border:1px solid gainsboro;
}
.salesReturn_box_un{
    position:relative;
    margin-right:.2rem;
    margin-top:.2rem;
}
.salesReturn_box_un_img{
    display:block;
    width:2rem;
    height:2rem;
}
.salesReturn_box_del{
    position:absolute;
    right:0;
    top:0;
    width:.4rem;
    height:.4rem;
    background-color:#000;
    display:flex;
    align-items:center;
    justify-content:center;
}
.salesReturn_box_del>img{
    width:.2rem;
    height:.2rem;
}
</style>


