<template>
<div v-title data-title="分类">
    <div class="box">
        <ul class="box-left">
            <li 
            v-for="item,index in titleTab" 
            @touchend="tabLeft(index)"
            class="list">
                <div :class='Tabindex == index ?"liitems" :""'>
                    <span>{{item.nam}}</span>
                </div>  
            </li>
        </ul>
        <div class="box-right">
            <div class="box-recommend">
                <img src="../imgs/my/my-youhui.png"/>
            </div>
            <div class="ifyTitle">
                /<span>{{titleTab[Tabindex].nam}}</span>/
            </div>
            <ul class="ifyList">
                <li @click="disPerse">
                    <div>
                        <img src="../imgs/home/default_img.png"/>
                    </div>
                    <p><span>精品鞋</span></p>
                </li>
            </ul>
        </div>
    </div>
</div>
</template>
<script>
    export default{
        name: 'category',
        data(){
            return {
                titleTab:[
                   {
                        nam:'女装'
                    },
                    {
                        nam:'女鞋'
                    },
                    {
                        nam:'女装'
                    }
                ],
                Tabindex:0
            }
        },
        created(){
            this.Tabindex = this.$route.params.id ? this.$route.params.id : 0
            this.$emit('select-type', 'category')
        },
        methods:{
            tabLeft (index) {
                this.Tabindex = index
            },
            getParams () {
                this.Tabindex = this.$route.params.id
            },
            disPerse () {
                //this.$router.push({path:"/pay",name:"pay",params:this.tabArr}) //跳转到下单页面
                this.$router.push({path:"/disperse"}) //跳转到商品匪类页面
            }
        },
        computed:{
        },
        watch:{
          
        }
    }    
</script>
<style>
    .box{
        position: fixed;
        top:0;
        left: 0;
        right: 0;
        bottom: 55px;
        display:flex;
       
    }
    .box-left{
        width: 1.4rem;
        height: 100%;
        border-right:1px solid #ededed ;
        padding-top:20px;
        box-sizing: border-box;
    }
    .list{
        width: 100%;
        height: 1.12rem;
        padding: .16rem 0;
        box-sizing: border-box;
        font-size:.26rem;
    }
    .list div{
        height: .8rem;
        width: 100%;
        line-height: .8rem;
        font-size:.26rem;
        text-align: center; 
        margin-right:-px; 
    }
    .liitems{
        color: red;
        font-weight: 600;
       
        border-right:2px solid red; 
    } 
    .box-right{
        width: 6.1rem;
        padding:20px .35rem 0;
       
    }
    .box-recommend{
        width: 100%;
    }
    .box-recommend img{
        width: 100%;
    }
    .ifyTitle{
        margin:.3rem;
        display:flex;
        align-items:center;
        justify-content:center;
        font-size: .3rem;
        font-weight: 600;
    }
    .ifyTitle span{
        margin: 0 .3rem;
    }
    .ifyList{
        display: flex;
        flex-wrap: wrap;
    }
    .ifyList li{
        font-size: .24rem;
        width: 25%;
        margin-bottom:.2rem;
        text-align: center;
    }
    .ifyList div{
        margin: 0 auto;
    }
    .ifyList li img{
        width: 1.2rem;
        height: 1.2rem
    }
    .ifyList li p{
        margin-top:.1rem 
    }
    .ifyList li span{
       text-align: center;
       font-size: .24rem;
     
    }
</style>


