<template>
<div  v-title data-title="我的订单">
    <ul class="orderForm_tab">
        <li v-for="item ,index in orderList" @click="orderMode(index)" :class="orderListState == index?'orderForm_tab_li':''">
            <span>{{item.name}}</span>
        </li>
    </ul>
    <div class="">
        <ul class="orderForm_box">
            <li >
                <div class="order_num">
                    <span>订单编号：sdjf</span>
                    <b>待付款</b>
                </div>
                <div @click="orderDetail" class="order_detail">
                    <div><img/></div>
                    <div class="order_detail_box">
                        <div class="order_detail_box_1">
                            <span>精品鞋</span>
                            <b>￥121</b>
                        </div>
                        <div class="order_detail_box_2">
                            <span>颜色： 红</span>
                            <span>套餐： 裸</span>
                        </div>
                        <div class="order_detail_box_3">
                            X4
                        </div>
                    </div>
                </div>
                <div class="order_pay">
                    <div></div>
                    <span>实付： ￥232</span>
                </div>
                <div class="order_button">
                    <p></p>
                    <div>
                        <button class="order_button1">取消订单</button>
                        <button class="order_button2">去付款</button>
                    </div>
                </div>
            </li>
        </ul>
        <div class="orderForm_box2">暂无订单</div>
    </div>

</div>
</template>
<script>
    let orderFormTab = [
        {
            name:"全部"
        },
        {
            name:"待付款"
        },
         {
            name:"待发货"
        },
        {
            name:"已发货"
        },
        {
            name:"待评价"
        },
    ]

    export default{
        data(){
            return{
                 orderList: orderFormTab,
                 orderListState:0
            }
        },
        methods:{
            orderMode(index){
                this.orderListState = index
            },
            orderDetail(){
                alert(window.location.href)
     
            //    wx.onMenuShareTimeline({
            //         title: '21', // 分享标题
            //         link: '21', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            //         imgUrl: '21', // 分享图标
            //         success: function () {
            //         // 用户确认分享后执行的回调函数
            //         console.log(2)
            //         },
            //         cancel: function () {
            //             console.log(1)
            //             // 用户取消分享后执行的回调函数
            //         },
            //         fail:function(){
            //              console.log(2)
            //         }
            //     });
                wx.config({
                    // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    appId: 'wxe921a2ee51341c25', // 必填，公众号的唯一标识
                    timestamp: '', // 必填，生成签名的时间戳
                    nonceStr: '', // 必填，生成签名的随机串
                    signature: '',// 必填，签名
                    jsApiList: [] // 必填，需要使用的JS接口列表
                });
           
               // this.$router.push({path:'/orderDetail'})
            }
        }
    }
</script>
<style>
    .orderForm_tab{
        margin-top:15px;
        border-bottom:1px solid #ededed;  
        display: flex;
        justify-content:space-around;
    }
    .orderForm_tab li{
        padding-bottom:9px;
        margin: 0 13px;
        margin-bottom:-1px;
        
    }
    .orderForm_tab_li{
        border-bottom:2px solid red; 
        color:red 
    }
    .orderForm_tab span{
        font-size: 14px;
    }
    .orderForm_box{
        padding:.2rem 0 .3rem .2rem;
        border-top:20px solid #f8f8f8;
    }
    .order_num{
        display:flex;
        flex-direction:row;
        align-items:center;
        width:100%;
        border-bottom:1px solid #e5e5e5;
        padding-bottom:.2rem;
        font-size:.3rem;
    }

    .order_num b{
        position:absolute;
        right:.3rem;
        color:red
    }
    .order_detail{
        display:flex;
        flex-direction:row;
        width:100%;
        padding-top:.2rem;
    }
    .order_detail img{
        width:1.5rem;
        height:1.5rem;
        background:red
    }
    .order_detail_box{
        display:flex;
        flex-direction:column;
        width:100%;
        margin-left:.2rem;
    }
    .order_detail_box_1{
        display:flex;
        flex-direction:row;
        align-items:center;
        vertical-align:middle;
        font-size:.28rem;
    }
    .order_detail_box_1 span{
        margin-right:1.5rem;

    }
    .order_detail_box_1 b{
        position:absolute;
        right:.2rem;
    }
    .order_detail_box_2{
        display:flex;
        margin-top:.2rem;
        flex-flow:row;
        align-items:center;

    }
    .order_detail_box_2 span{
        margin-right:.2rem;
        color:gray;
        font-size:.25rem;
    }
    .order_detail_box_3{
        margin-top:.3rem;
        font-size:.25rem;
        color:gray;
    }
    .order_pay{
        display:flex;
        flex-direction:row;
        align-items:center;
        padding:.2rem 0 .2rem .2rem;
    }
    .order_pay div,.order_button p{
        flex:1
    }
    .order_pay span{
        font-size:.28rem;
        color:#000;
    }
    .order_button{
        display:flex;
        flex-direction:row;
        align-items:center;
        padding-top:.3rem;
        padding-right:.2rem;
        padding-bottom:.3rem;
        border-top:1px solid #e5e5e5;
    }
    .order_button div{
        display:flex;
        flex-direction:row;
        align-items:center;
    }
    .order_button button{
        width:1.8rem;
        font-size:.25rem;
        border-radius:.02rem;
        vertical-align:middle;
        height:.5rem
    }
    .order_button1{
        border:1px solid #000;
        color:#000
    }
    .order_button2{
        border:1px solid red;
        color:red;
        margin-left:.2rem;
    }
    .orderForm_box2{
        text-align:center;
        font-size:.3rem;
        color:#000;
        padding:1rem
    }
</style>


