<template>
<div  v-title data-title="商品详情">
    <div class="dity_box">
        <div class="combanner">
            <div class="swiper-container_dity">
                <div class="swiper-wrapper">
                    <div class="swiper-slide"><img  style="background:red"/></div>
                    <div class="swiper-slide"><img  style="background:#000"/></div>
                    <div class="swiper-slide"><img  style="background:yellow"/></div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
        </div>   
        <div class="aomDity">
            <div class="aomDityTop">
                <p class="dity_tiele"><span>上装难以</span></p>
                <p class="dity_q">
                    <span>￥252.3</span>
                </p>
                <p class="dity_h"><span>运费：￥6</span></p>
            </div>
            <div class="dity_specification">
                <div class="dity_specification_top" @touchend="pop_up=true" > 
                    <span>选择规格</span>
                    <b>></b>
                </div>
                <div class="dity_specification_bottom" >
                    <b>折</b>
                    <span>第二件优惠价格:￥100.00</span>
                </div>
            </div>
        </div>
        <div class="dity_tab">
            <ul class="dity_tab_tabber" >
                <li v-for="item,index in tabArr" >
                    <a :class="borderTab==item.to?'dity_tab_tabber_is_on':''" @touchend="rouTab(item.to,index)" href="javascript:;">{{item.title}}</a>
                </li>
            </ul>
            <div class="">
                <router-view/>
            </div>
        </div>
    </div>
    <div class="footer_product">
        <div class="dity_collect" @touchend="dityCollect">
            <img v-if="collect" src="../imgs/product/product-collect.png"/>
            <img v-else src="../imgs/product/product-collect-select.png"/>
        </div>
        <div class="dity_shopping" @click="cart">
            <span>1</span>
            <img src="../imgs/toolbar/cart.png"/>
        </div>
        <div class="goumai goumaijiaru" @click="pop_upPay(1)">
            <span>立即购买</span>
        </div>
        <div class="jiaru goumaijiaru" @click="pop_upPay(2)">
            <span>加入购物车</span>
        </div>
    </div>
    <div class="pop_up" v-show="pop_up">
        <div class="pop_up_bottom">
            <dl class="pop_up_img">
                <dd>
                    <img src="../imgs/home/default_img.png"/>
                </dd>
                <dt>
                    <div class="pop_up_title">
                        <img src="../imgs/product/rule-close.png" @touchend="pop_up=false"/>
                        <div>
                            <span>速度发货思路发生丢打飞机圣诞节</span>
                        </div>
                        
                    </div>
                    <p>￥1162</p>
                </dt>
            </dl>
            <div class="pop_up_xuanze">
                <p>套餐</p>
                <div>
                    <span 
                    v-for="item,index in pop_up_i" 
                    :class="pop_up_ictions == index?'pop_up_iction':''"
                    @click="pop_up_ictions=index"
                    >{{item}}</span>
    
                </div>
            </div>
            <div class="pop_up_num">
                <span>购买数量</span>
                <div>
                    <b @touchend="num_jian">-</b>
                    <span>{{pop_upnum}}</span>
                    <b @touchend="num_jia">+</b>
                </div>
                
            </div>
            <mt-button v-if="pop_up_button" type="danger" @click="goumai" class="pop_up_button">立即购买</mt-button>
            <mt-button v-else type="danger" @click="jiaru" class="pop_up_button">加入购物车</mt-button>
        </div>
    </div>
</div>   
</template>
<script>
var tabArr = [
    {
        to:"particulars",
        title:"商品详情"
    },
     {
        to:"parameter",
        title:"产品参数"
    },
     {
        to:"afteSale",
        title:"售后保障"
    },
     {
        to:"appraise",
        title:"评价"
    },
] 
var pop_up_i = ['单桌套餐','一桌思椅','单独餐椅']
import { Toast } from 'mint-ui';
 export default { 
     data(){
         return{
             pop_up:false,
             pop_up_button:true,
             pop_upnum:1,
             tabArr:tabArr,
             collect:true,
             pop_up_i:pop_up_i,
             pop_up_ictions:-1,
             borderTab:'particulars'
         }
     },
     created(){
         setTimeout(function () {
            var mySwiper = new Swiper('.swiper-container_dity', {
                  autoplay:{
                      delay: 1000,//1秒切换一次
                      disableOnInteraction: false,
                  },//自动滚动
                  loop:true,//循环
                  speed:1000,//滚动速度
                  loop:true,
                  pagination: {
                    el: '.swiper-pagination'
                  },
              })
            }, 0)
        this.borderTab = this.$route.name
     },
     methods:{
         goumai () { //立即购买
            if(this.pop_up_ictions == -1){
                Toast({message: '请选择商品属性', duration: 2000});
                return                
            }
            this.$router.push({path:"/pay",name:"pay",params:this.tabArr}) //跳转到下单页面
         },
         jiaru () { // 加入购物车
           if(this.pop_up_ictions == -1){
                Toast({message: '请选择商品属性', duration: 2000});
                return
            }
         },
         pop_upPay(pop){
            this.pop_up = true
            if(pop==1){ //立即购买
                this.pop_up_button=true
            }else if(pop==2){ //加入购物车
                this.pop_up_button=false
            }
         },

         num_jian () { //商品数量减
             if(this.pop_upnum <=1 ) return
             this.pop_upnum--
         },
         num_jia () { //商品数量加
             this.pop_upnum++
         },
         cart (e) { //  购物车
            let ev = e||event
            ev.stopPropagation()
            this.$router.push({ path:'/cart'})
         },
         dityCollect(){ //收藏
            this.collect = !this.collect
            if(this.collect ){ 
                Toast({
                        message: '取消成功',
                        duration: 1000
                    });
            }else{
                Toast({
                        message: '收藏成功',
                        duration: 1000
                    });
            }
         },
         rouTab(to){
            this.borderTab = to
            this.$router.push({path:to})
         }
     },
     watch:{
      
     }
 }
</script>
<style>
.combanner{
    width: 7.5rem;
    height: 7.5rem;   
    overflow:hidden;
    position:relative
}

.combanner img{
    width: 7.5rem;
    height: 7.5rem;  
}
.dity_box{
    margin-bottom:1.3rem; 
}
.aomDity{
    width: 100%;
    padding: 0 .4rem;
    box-sizing: border-box;
}
.aomDityTop{
    padding-top: .5rem;

}
.dity_tiele{
    font-size:.35rem; 
    color: #000;
}
.dity_q{
    color: #ff4646;
    padding-top:.26rem; 
}
.dity_q span {
    font-size: .3rem
}
.dity_h{
    color: #929191;
    font-size: .26rem;
    padding-top: .25rem; 
}
.dity_specification{
   
    margin-top:.4rem ;
}
.dity_specification_ b{
    float: right;
}
.dity_specification_top{
    color: #000;
    font-size: .26rem;
    line-height: .8rem;
    border-bottom:.01rem solid #e5e5e5
}
.dity_specification_top b{
    float: right;
}
.dity_specification_bottom{
    color: #ff4646;
    line-height: .8rem;
    font-size: .26rem;
}
.dity_specification_bottom b{
    border: 1px solid #ff4646;
    font-size: .22rem;
    margin-right: .1rem
}
.dity_tab{
    margin-top:.2rem; 
}
.dity_tab_tabber{
    display: flex;
    justify-content: space-between;
    align-content: center;
    border-bottom:.01rem solid #e5e5e5
}
.dity_tab_tabber li{
    width: 25%;
    text-align: center;
    color: #1f1f1f;
    font-size:.26rem 
}
.dity_tab_tabber li a{
    display: inline-block;
    padding-bottom: 0.2rem;
    margin-bottom:-0.01rem;  
}
.dity_tab_tabber_is_on{
    border-bottom:.03rem solid #000; 
    color: #000;
}
.footer_product{
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1rem;
    border-top:1px solid #ccc;
    border-bottom:1px solid #ccc;
    background: #fff;
    z-index: 10;
    display: flex;
}
.dity_collect{
    width: 1.47rem;
    border-right:.01rem solid #ccc;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer_product img{
    width: .32rem;
    height: .32rem;
}
.dity_shopping{
    width: 1.72rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.dity_shopping span{
    position: absolute;
    width:.3rem;
    height:.3rem;
    border-radius:.3rem;
    background-color:#ff4646;
    text-align:center;
    line-height:.3rem;
    font-size:.14rem;
    color:white;
    position:absolute;
    top:.22rem;
    margin-left:.22rem;
} 
.goumaijiaru{
    font-size:.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    width: 2.15rem;
}
.goumai{
    background: #df4949;
}
.jiaru{
    background: #d03333;
}
.pop_up{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index:122;
    background:rgba(0, 0, 0 ,.7);
    
}
.pop_up_bottom{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    padding-bottom: .3rem 
}
.pop_up_img{
    width: 100%;
    padding: 0 .4rem;
    height: 2.06rem;
    display: flex;
    box-sizing: border-box;
    
}
.pop_up_img dd{
    width: 2.61rem;
    height: 2.61rem;
    border: .01rem solid #e5e5e5;
    padding:  .04rem;
    box-sizing: border-box;
    background: #fff;
    margin-top: -0.56rem 
}
.pop_up_img dd img{
    width: 2.51rem;
    height: 2.51rem;
}
.pop_up_img dt{
    margin-left:.35rem; 
    width: 3.7rem
}

.pop_up_title div{
    font-size: .3rem;
    color: #000;
    line-height: .4rem;
    width: 3.1rem;
    height: .8rem;
}
.pop_up_title img{
    width:.35rem;
    height:.35rem;
    float: right;
} 
.pop_up_title{
    margin-top:.35rem; 
} 
.pop_up_img dt p{
    color:red;
    font-size:.3rem;  
    margin-top:.3rem ;
    text-align: left  
} 
.pop_up_num{
    width: 100%;
    padding: .2rem .4rem; 
    box-sizing: border-box;
    font-size: .26rem;
    line-height: .66rem
}
.pop_up_num div{
    float: right;
}
.pop_up_num div b{
  margin: .2rem;
}
.pop_up_button{
    width: 6.7rem;
    margin: 0 .4rem
    
}
.pop_up_xuanze{
    padding: .2rem .4rem;
    color: #000;
}
.pop_up_xuanze p {
    font-size: .26rem;
}
.pop_up_xuanze span{
    font-size: .22rem;
    display: inline-block;
    width: 1.5rem;
    margin-right: .18rem;
    height: .45rem;
    text-align: center;
    line-height: .45rem;
    border: 1px solid #d2d2d2;    
}
.pop_up_xuanze .pop_up_iction{
    background: #000;
    color: #fff
}
.pop_up_xuanze div{
    margin-top:.3rem 
}
</style>


