import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
Vue.use(Router)
const _impot = require('./nodeadd')
const _i = require('./node2')

const a = require( '@/views/login').default
export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:"/home",
      children:[
        {
          path:'home', 
          name:'home',
          hidden:true,
          component:_impot('home')  //首页
          
        },
        {
          path:"category",
          name:'category',
          hidden:true,
          component:_impot('classify') // 分类
        },
        {
          path:"cart",
          name:"cart",
          hidden:true,
          component:_impot('cart')   // 购物车
        },
        {
          path:"my",
          name:'my',
          hidden:true,
          component:_impot('my')   // 我的
        },
        {
          path:'login',
          name:'login',
          redirect:'login/c',
          component:_i('login'),
          children:[  
            {
              path:'c',
              name:'c',
              component:_i('comdity/particulars')
            }
          ]
        }
      ]
    },
    {
      path:"/commodity",
      name:"commodity",
      component:_impot("commodity"),
      redirect:"/commodity/particulars",
      children:[
        {
          path:"particulars",//商品详情
          name:'particulars',
          component:_i('comdity/particulars')
        },
        {
          path:"appraise",//商品评价
          name:'appraise',
          component:_i('comdity/appraise')
        },
        {
          path:"afteSale",//商品售后
          name:'afteSale',
          component:_i('comdity/afteSale')
        },
        {
          path:"parameter",//商品参数
          name:'parameter',
          component:_i('comdity/parameter')
        }
      ],
    },
    {
      path:"/seek",  //搜索页面
      name:'seek',
      component:_impot('seek')
    },
    {
      path:"/addSite", //修改地址
      name:"addSite",
      component:_impot('site/addSite')
    },
    {
      path:"/site",   //添加地址
      name:"site",
      component:_impot('site/site')
    },
    {
      path:"/discountsPay",   //优惠券
      name:"discountsPay",
      component:_impot('pay/discountsPay')
    },
    {
      path:"/pay",   //下单页面
      name:"pay",
      component:_impot('pay/pay')
    },
    {
      path:'/disperse',  // 分类页面
      name:"disperse",
      component:_impot('disperse')
    },
    {
      path:"/orderForm", //订单页面
      name:"orderForm",
      component:_impot('orderForm')
    },
    {
      path:'/orderDetail', //订单详情
      name:"orderDetail",
      component:_impot('order/orderDetail')
    },
    {
      path:"/myCollect", //我的收藏
      name:'myCollect',
      component:_impot('myCollect')
    },
    {
      path:'/returnedGoods',//退货售后
      name:"returnedGoods",
      component:_impot('comdity/returnedGoods')
    },
    {
      path:"/message",  //消息中心
      name:'message',
      component:_impot('message')
    },
    {
      path:"/AboutUs",  //关于我们
      name:"AboutUs",
      component:_impot('AboutUs')
    },
    {
      path:"/salesReturn", //退货申请 评论
      name:'salesReturn',
      component:_impot('order/salesReturn')
    },
    {
      path:"/wallet", //红包
      name:"wallet",
      component:_impot('pay/wallet')
    },
    {
      path:"/result", //支付结果
      name:'result',
      component:_impot('pay/result')
    }
  ]
})





