<template>
<div class="site_box">
    <div class="site_box1" >
    <mt-field label="联系人" placeholder="请输入用户名" v-model="username"></mt-field>
     
    <mt-field label="手机号" 
    :state="isPoneAvailable" 
    placeholder="请输入联系人手机号" 
    type="tel" 
    v-model="phone"></mt-field>
    
    <mt-field label="地区"
    @focus.native.capture="handleCommentFocus"
    readonly
    placeholder="省/市/区" v-model="birthday"></mt-field>

    
  
    <mt-field label="详情地址" 
     placeholder="请输入详情地址"  
     type="text" 
     v-model="number"></mt-field>

    </div>
    <div class="site_box2">
        <button @touchend="saveSite">保存</button>
    </div>
    <mt-popup
    v-model="popupVisible"
    position="bottom"
    modal=rue
    closeOnClickModal=true
    >
        <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
    
</div>    
</template>
<script>
import { Picker } from 'mint-ui';   
import { Popup } from 'mint-ui';
    export default{
        data () {
           return {
                username:'', 
                email:'',
                password:'',
                phone:'',
                website:'',
                number:'', 
                birthday:'省/市/区',
                pickerSite:false,
                popupVisible:false,
                slots: [
                    {
                        flex: 1,
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                        className: 'slot1',
                        textAlign: 'left'
                    },
                    {
                        flex: 1,
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                        className: 'slot2',
                        textAlign: 'left'
                    },
                    {
                        flex: 1,
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                        className: 'slot3',
                        textAlign: 'left'
                    }
                ]
           }
        },
        methods:{
            onValuesChange(picker, values) {  //获取地区选择
                if (values[0] > values[1]) {
                    picker.setSlotValue(1, values[0]);
                    this.birthday = values
                }
                 this.birthday = values
            },        
            handleCommentFocus(){ //地区选择
                this.birthday
                this.popupVisible = true;   
            },
            saveSite(){ //保存
                
            },
        },
        computed:{
             isPoneAvailable() {  
                var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;  
                if(this.phone == '' ) return
                if (!myreg.test(this.phone)) {  
                    return 'error';  
                } else {  
                    return 'success'  
                }  
            }  
        }
    }
</script>
<style>

.site_box{
    width:100%;
    padding:0 .4rem;
    padding-top:.4rem;
    box-sizing: border-box;
}
.site_box .mint-cell-text{
    color: #dadada
}
.site_box .mint-cell:last-child{
    background-image: none
}
.site_box .mint-cell-wrapper{
    background-image: -webkit-linear-gradient(top, #e5e5e5, #e5e5e5 50%, transparent 50%);
    background-image: linear-gradient(180deg, #e5e5e5, #e5e5e5 50%, transparent 50%);
}
.site_box .mint-cell:first-of-type .mint-cell-wrapper{
    background-image:none
}
.site_box .mint-popup{

    width:100%;
}

.site_box2{
    height: .76rem;
    margin-top:.4rem;
    padding-right:.4rem;
}
.site_box2 button{
    width: 100%;
    height: .76rem;
    background: none;
    border: 1px solid #919191;
    color: #919191;
    font-size: .23rem;
    outline:none;
    border:1px solid grey;
    border-radius:2px;
}
</style>


