<template>
<div class="addSite_box"  v-title data-title="收货地址">
    <ul class="addSite_box1">
        <li>
            <div  class="addSite_box1_name">
                <span>水电费</span>
                <b>1234567985</b>
            </div>
            <div class="addSite_box1_site">
                <span>东城区  北京  河北</span>
                <img 
                class="addSite_box1_site_dle" 
                src="../../imgs/adress/edit-address.png"
                @click="redactSite"
                />
                
                <img 
                src="../../imgs/adress/del-adress.png"
                @click="deleteSite"
                />
            </div>
            <div class="addSite_box1_default">
            <i 
            :class="checkbox?'weui_icon_success':'weui_icon_circle'" 
            @click="optForSite"></i>
                <span 
                :class="checkbox?'addSite_box1_circle':''" 
                >{{checkbox?'默认地址':'设为默认地址 '}}</span>
            </div>
        </li>
    </ul>
    <div class="addSite_box2">
        <button @click="addLocation">+添加收货地址</button>
    </div>
</div>
</template>
<script>
    export default{
        data () {
            return {
                checkbox:true
            }
        },
        methods:{
            optForSite(){ //选择默认地址

            },
            redactSite(){ //修改地址
                
            },
            deleteSite(){ //删除地址

            },
            addLocation(){
                console.log(1)
                 this.$router.push({ path: '/site' })
            }
        }
    }
</script>
<style>
    .addSite_box{
        width:100%;
    }
    .addSite_box1{
        width: 100%;
    }
    .addSite_box2{
        width: 100%;
        padding: 0 .4rem;
        box-sizing: border-box;
        position: fixed;
        height: .76rem;
        left: 0;
        right: 0;
        bottom: .33rem;
    }
    .addSite_box2 button{
        width: 100%;
        height: .76rem;
        background: none;
        border: 1px solid #919191;
        color: #919191;
        font-size: .23rem;
        outline:none;
    }
    .addSite_box1{
        width: 100%;
        padding: 0 .4rem;
        box-sizing: border-box;
        margin-top:.5rem
    }
    .addSite_box1 li{
        width: 100%;
        border-bottom: 1px solid #e5e5e5
    }
    .addSite_box1_name{
        font-size: .3rem;
        font-weight: 600;
        color: #000
    }
    .addSite_box1_name span{
        margin-right:.5rem 
    }
    .addSite_box1_site{
        display:flex;
        flex-flow:row;
        align-items:center;
        margin-top:.3rem;
    }
    .addSite_box1_site span{
        color: #202020;
        font-size:.25rem;
     
        flex:1;

    }
    .addSite_box1_site img{
        width: .3rem;
        height: .32rem;
    }
    .addSite_box1_site_dle{
        margin-right: 1rem
    }
    .addSite_box1_default{
        display:flex;
        flex-flow:row;
        width:100%;
        align-items:center;
        margin:.5rem 0;
        color: #C9C9C9;
    }
    .addSite_box1_default .addSite_box1_circle{
        color: red
    }
    .addSite_box1_default i:before{
        font-size:.4rem;
    }
    .addSite_box1_default .weui_icon_success:before {
        color: red;
    }
    .addSite_box1_default span{
        font-size:.25rem;
        margin-left:.2rem;        
    }
</style>


