            <div class="result_box_pay3">
                <button @click="checkDdan">立即查看</button>
            </div>
        </div>
        <div class="result_box_share">
            <div class="result_box_share_q">
                <img src="../../imgs/icon/to-get-package.png"/>
            </div>
            <div class="result_box_share_z">转发至微信好友，微信群，与好友一起抢手气红包</div>
            <div class="result_box_share_shou">
