<script>
    export default{
        data(){
            return{
                pop:false,
                regulaionindex:false
            }
        },
        methods:{
            regulation(){// 活动规则
                this.regulaionindex = false
            },
            actionRule(){ // 活动规则
                this.regulaionindex = true
            },
            checkDdan(){ //跳转订单页面
                 this.$router.push({path:'/orderForm'})
            },
