        data () {
            return {
                sheetVisible:false,
                invoice:"无",
                inputPay:false,
                listItem:"",
                dizhi:true,
                actions:[
                    {
                        name:"无",
                        method:(val)=>{
                            this.invoice = val.name
                            this.inputPay = false
                        }
                    },
                    {
                        name:"个人发票",
                        method:(val)=>{
                            this.inputPay = false
                            this.invoice = val.name
                        }
                    },
                    {
                        name:"企业发票",
                        method:(val)=>{
                            this.inputPay = true
                            this.invoice = val.name
                        }
                    },
                ]
            }
        },
        methods:{
            fapiao(){
                this.sheetVisible=true
            },
            adddizhi(){
                this.$router.push({path:'/site'})
            },
            xiugaidizhi(){
                 this.$router.push({path:'/addSite'})
            }
            
        },
        created(){
            this.listItem = this.$route.params
        },
        computed:{
            abc(){
                this.listItem = this.$route.params
            }
        },
        watch:{
           'listItem':function(v){
               console.log(1,v)
           }
        }
    }
