<template>
<div v-title data-title="购物车">
         
    <ul class="cart_box ">
        <li 
        v-for="item,index in listItem"
        :class="signIndex==index?'touch-move-active':''"
        >
            <div class="cart_box_item"
            @touchstart="startdow" 
            @touchmove="huadong(index)"  
            @touchend="xingqingye" 
            >
                <div class="cart_box_item_checkbox">
                    <i 
                    :class="item.status?'weui_icon_success':'weui_icon_circle'"
                    @touchend="checkbox_d(index)" 
                    ></i>
                </div>
                <div class="cart_box_item_img">
                    <img src="../imgs/home/default_img.png"/>
                </div>
                <div class="cart_box_item_word">
                    <div class="cart_box_item_word1">
                        <span>十多个后方可</span>
                        <b>￥{{item.price}}</b>
                    </div>
                    <div class="cart_box_item_word2"></div>
                    <div class="cart_box_item_word3">
                        <div class="cart_box_item_jia">
                            <div class="cart_box_item_ji" 
                             @click="minnum(index)" 
                            >-</div>
                            <div class="cart_box_item_num">{{item.quantity}}</div>
                            <div class="cart_box_item_jian"
                             @click="addnum(index)" 
                            >+</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="cart_box_item_d"  @click="removeCommodity(index)" >
                <img src="../imgs/icon/cart-del.png"/>
                <span>删除</span>
            </div>
        </li>
    </ul>
    
    <div class="cart_box_tab">
        <div class="cart_box_box_">
            <i 
            :class="checkbox?'weui_icon_success':'weui_icon_circle'" 
            @touchend="quanxuan"></i>
            <!-- 样式  weui_icon_success <img src="../imgs/adress/default-adress-nomal.png"/> 切换 <img src="../imgs/adress/default-adress-select.png"/> -->
            <span>全选({{selectedTypeCounts}})</span>
        </div>
        <div class="cart_box_heji">
            <span>合计：</span>
            <b>￥{{selectprice}}</b>
        </div>
        <div class="cart_box_submit" @click="cart_box_submit">结算</div>
    </div>

</div>    
</template>
<script>
    let listItem = [
        {
            status:false,
            quantity:1,
            price:123.1
        },
        {
            status:false,
            quantity:1,
            price:123.5
            
        },
        {
            status:false,
            quantity:1,
            price:123.1
        },
        {
            status:false,
            quantity:1,
            price:123.5
            
        },
                {
            status:false,
            quantity:1,
            price:123.1
        },
        {
            status:false,
            quantity:1,
            price:123.5
            
        }
    
    ]
    import { MessageBox } from 'mint-ui';
    export default{
        name: 'cart',
        data(){
            return {
               startend:false,
               page_X:"",
               page_Y:"",
               signIndex:-1,
               listItem:listItem,
               checkbox:false,
               selectedCounts:0
            }
        },
        created(){
           this.$emit('select-type', 'cart')
        },
        computed:{
            selectedTypeCounts(){ //全部几件商品
                let n = 0
                for(let i=0;i<this.listItem.length;i++){
                    if(this.listItem[i].status){
                        n += this.listItem[i].quantity
                    }
                }
                return n
            },
            selectprice(){ //商品总价
                let n = 0
                let p = Math.pow(10,10)
                for(let i=0;i<this.listItem.length;i++){
                    if(this.listItem[i].status){
                        n += Number(this.listItem[i].price)*p * this.listItem[i].quantity
                        }
                }
                n = n/p
                return n
            }
        },
        methods:{
            huadong(index){  //左滑删除
                let ev = event
                this.startend = false 
                let e = ev.touches[0]
                var that = this,
                startX = that.page_X,//开始X坐标
                startY = that.page_X,//开始Y坐标
                touchMoveX = e.clientX,//滑动变化坐标
                touchMoveY = e.clientY,//滑动变化坐标
                //获取滑动角度
                angle = that.angle({ X: startX, Y: startY }, { X: touchMoveX, Y: touchMoveY });
                if (Math.abs(angle) > 84) return;
                if (touchMoveX > startX) {//右滑
                    if(that.signIndex == index) 
                        that.signIndex = -1
                } else {
                  that.signIndex = index
                }
            },
            xingqingye(){ // 点击进入详情页
                if(!this.startend)  return
                this.startend = false
                console.log('进入详情页')
            },
            startdow(ev){ // 手指触摸事件
                this.startend = true // 记录是否是滑动
                let that = this
                let e = ev.touches[0]
                that.page_X = e.clientX
                that.page_Y = e.clientY
            },
            angle (start, end) {
                var _X = end.X - start.X,
                _Y = end.Y - start.Y
                //返回角度 /Math.atan()返回数字的反正切值
                return 360 * Math.atan(_Y / _X) / (2 * Math.PI);
            },
            checkbox_d(index){ //选中按钮   
                event.stopPropagation()
                this.listItem[index].status = !this.listItem[index].status
                this.quanxuan_q()
            },
            quanxuan_q(){
                this.selectedCounts=0
                for(let i=0;i<this.listItem.length;i++){
                   if(this.listItem[i].status){
                        this.selectedCounts++
                   }
                }
                if(this.selectedCounts == this.listItem.length){
                    this.checkbox = true
                }else{
                    this.checkbox = false
                }
            },
            quanxuan(){ //全选按钮
                this.checkbox = !this.checkbox  
                for(let i=0;i<this.listItem.length;i++){
                   this.listItem[i].status = this.checkbox   
                }
            }, 
            addnum(i){ //商品加
                event.stopPropagation()
                this.listItem[i].quantity++ 
            },
            minnum(i){ //商品减
                event.stopPropagation()
                if(this.listItem[i].quantity <= 1) return
                this.listItem[i].quantity-- 
            },
            removeCommodity(i){//删除商品
                event.stopPropagation()
                let thas = this
                MessageBox.confirm('您真的要删除吗?').then(action => {
                    thas.listItem.splice(i,1);
                    thas.signIndex= -1
                    this.quanxuan_q()
                });
               
            },
            cart_box_submit(){
                this.$router.push({path:"/pay",name:'pay',params:this.listItem})
            }
        },
        watch:{
          
        }
    }
</script>
<style>
.cart_box{
    width:100%;
    margin-bottom:2rem;

}
.cart_box li{
    padding:.2rem 0;
    background-color:#fff;
    border-bottom:.2rem solid #f8f8f8;
    height:1.86rem;
    width:100%;
    display:flex;
}
.cart_box_item{
    width:100%;
    display:flex;
    margin-left:-1.4rem;
    -webkit-transition:all 0.4s;
    transition:all 0.4s;
    -webkit-transform:translateX(1.4rem);
    transform:translateX(1.4rem);
}

.cart_box_item_checkbox{
    width:.84rem;
    display:flex;
    align-items:center;
    justify-content:center;
}    
.cart_box_item_checkbox i:before{
    font-size: .44rem
}
.cart_box_item_img{
    width:1.86rem;
    height:100%;
    background-color:#f5f6f5;
    border-radius:4px;
    
}
.cart_box_item_img img{
    width: 100%;
    height:100%;
}
.cart_box_item_word{
    display:flex;
    flex:1;
    flex-direction:column;
    justify-content:space-between;
    padding:.08rem 0;
    margin:0 .2rem;
    box-sizing:border-box;
    color:#454553;
}
.cart_box_item_word1{
    display:flex;
    justify-content:space-between;
}
.cart_box_item_word1 span{
    font-size: .26rem;
    color: #000;
    flex:3;
}
.cart_box_item_word1 b{
    font-size: .26rem;
    color: red;
    flex:1;
    white-space:nowrap;

}  
.cart_box_item_word3{
    font-size:.24rem;
    display:flex;
    align-items:center;
    justify-content:space-between;
    
}
.cart_box_item_word2{
    display:flex;
    flex-direction:row;
    align-items:center;
}
.cart_box_item_jia{
    width:2.2rem;
    display:flex;
    justify-content:space-between;
    text-align:center;
}
.cart_box_item_jia div{
    font-size: .5rem
}
.cart_box_item_jia .cart_box_item_num{
    font-size: .25rem;
    line-height: .7rem;
}
.cart_box_item_num{

}
.cart_box_item_ji{

}
.cart_box_item_ji,.cart_box_item_jian{
    display:flex;
    justify-content:center;
    align-items:center;
    
}
.cart_box_item_d{
    display:flex;
    height:1.86rem;
    width:1.4rem;
    flex-direction:column;
    background-color:red;
    align-items:center;
    justify-content:center;
    margin-left:.2rem;
    -webkit-transform:translateX(1.4rem);
    transform:translateX(1.4rem);
    -webkit-transition:all 0.4s;
    transition:all 0.4s;
    
}
.cart_box_item_d img{
    width:.37rem;
    height:.43rem;
    
}
.cart_box_item_d span{
    font-size:.3rem;
    color:white;
    background-color:transparent;
    
}
.touch-move-active .cart_box_item,
.touch-move-active  .cart_box_item_d {
    -webkit-transform: translateX(0);
    transform: translateX(0);
}
.cart_box_tab{
    position:fixed;
    bottom:1rem;
    height:1rem;
    width:100%;
    display:flex;
    border-top:1px solid #ccc;
    align-items:center;
    background-color:white;
}
.cart_box_box_{
    display:flex;
    flex-direction:row;
    align-items:center;
    flex:1;
    font-size:.28rem;
    width:40%;
}
.cart_box_box_ i:before{
    font-size: .4rem;
    margin:0 .2rem;
  
}
.weui_icon_circle:before{
    color: #dcdcdc
}
 .weui_icon_success:before{
    color: #e01a1a
}
.cart_box_heji{
    display:flex;
    flex-direction:row;
    align-items:center;
    margin-right:.4rem;
    font-size: .3rem;
}
.cart_box_heji span{
    color: #919191
}
.cart_box_heji b{
    color: red;
}
.cart_box_submit{
    width:2.5rem;
    color:white;
    height:1rem;
    background-color:red;
    text-align:center;
    justify-content:center;
    vertical-align:middle;
    line-height:1rem;
    font-size:.3rem;

}
.abc{

}
</style>


