<template>
<div>
    <div class="container">
        <div class="search">
            <input  type="text" v-model="value1" placeholder="输入要搜索的商品">
            <img v-show="value1 != ''" src='../imgs/product/rule-close.png' @touchend="deleck"/>
        </div>
        <span @touchend="retreat">取消</span>
    </div>
    <goods class="list_seek" :listTime='arr'/>
</div>
</template>
<script>
    import goods from './comdity/goods'
    export default {
      data(){
        return {
          value1:"",
          arr:[1,2]
        }
      },
      components:{
        goods
      },
      computed:{

      },
      methods:{
        retreat () { //取消
           this.$router.go(-1) 
        },
        deleck () { // 删除
          this.value1=""
        },
        commodity () {  
          this.$router.push({path:"/commodity"})
        }
      },
      watch:{
        "value1": val => {
          console.log(val) 
        }
      }
    }
</script>
<style>
.container{
    top:0;
    left: 0;
    height: 36px;
    margin-top: 25px;
    position: fixed;
    width:100%
}
.list_seek{
   margin-top:52px;
}
.search {
  display: flex;
  flex-direction: row;
  align-items: center;
  
  /* width: 100%; */
  padding:0 .3rem;
  height: 36px;
  background: #f5f5f5;  
  width: 5.9rem;
  box-sizing: border-box;
  margin-left: .26rem;
  float: left;
}
.input-view{
    height: 36px;
}
.search input {
    color: gray;
    height: .6rem;
    padding-left: .4rem;
    flex: 1;
    font-size: 15px;
    border: 0;
    outline: 0;
    background: #f5f5f5;  
  }
  .search img {
    width: 20px;
    height: 20px;
  }
  .container span{
    font-size: 15px;
    float: right;
    margin-right: .42rem;
    line-height: 36px
  }

 
</style>


