<template>
    <div  v-title data-title="消息中心">
        <ul class="orderForm_tab">
            <li v-for="item ,index in ['消息信息','物流信息']" @click="onMessage(index)" :class="orderListState == index?'orderForm_tab_li':''">
                <span>{{item}}</span>
            </li>
        </ul>
        <div v-if="orderListState == 0" class="orderForm_box">
            <div class="orderForm_box_yes">
                <p>新消息推送</p>
                <div>2018 04 28</div>
            </div>
            <div class="orderForm_box_yes">
                <p>新消息推送</p>
                <div>2018 04 28</div>
            </div>
            <div class="orderForm_box_no">您还没有任何消息</div>
        </div>
        <div v-if="orderListState == 1" class="orderForm_box">
            <div class="orderForm_box_yes2">
                <p>新消息推送</p>
                <div>2018 04 28</div>
            </div>
            <div class="orderForm_box_no">您还没有任何消息</div>
        </div>
    </div>
</template> 
<script>
    export default{
        data(){
            return {
                orderListState:0
            }
        },
        methods:{
            onMessage(index){
                this.orderListState = index

            }
        }
    }
</script>
<style>
 .orderForm_tab{
        margin-top:15px;
        border-bottom:1px solid #ededed;  
        display: flex;
        justify-content:space-around;
    }
    .orderForm_tab li{
        padding-bottom:9px;
        margin: 0 13px;
        margin-bottom:-1px;
        
    }
    .orderForm_tab_li{
        border-bottom:2px solid red; 
        color:red 
    }
    .orderForm_tab span{
        font-size: 14px;
    }
    .orderForm_box{
        padding-top:.2rem
    }
    .orderForm_box_yes{
        display:flex;
        width:100%;
        flex-direction:column;
        justify-content:center;
        background-color:#f8f8f8;
    }
    .orderForm_box_yes>p{
        font-size:.3rem;
        background-color:white;
        padding:.3rem .5rem
    }
    .orderForm_box_yes>div{
        font-size:.25rem;
        color:gray;
        border-bottom:1px solid gainsboro;
        padding-bottom:.3rem;
        margin-bottom:1px;
        background-color:white;
        padding-left:.5rem;
    }
    .orderForm_box_yes2{
        display:flex;
        width:100%;
        flex-direction:column;
        justify-content:center;
        background-color:#f8f8f8;
    }
    .orderForm_box_yes2>div{
        font-size:.3rem;
        padding:.3rem .5rem;
        background-color:white;
        margin-bottom:.2rem;
    }
    .orderForm_box_yes2>p{
        font-size:.25rem;
        color:gray;
        text-align:center;
        border-bottom:1px solid gainsboro;
        padding:.3rem 0;
        background-color:white;
    }
    .orderForm_box_no{
        text-align:center;
        font-size:.3rem;
        padding-top:.5rem;
    }
</style>


