<template>
<div>
    <ul class="disperse_tab">
        <li @click="disperse_tabbar(index)" v-for="item,index in [全部,全部,全部,全部,全部]" :class="disperse_tabIndex==index?'disperse_tab_li':''">
            <span>全部</span>
        </li>
    </ul>
    <Goods :listTime='arr'/>
</div>
</template>
<script>
    import Goods from './comdity/goods.vue'
    export default{
        data(){
            return{
                arr:[123],
                disperse_tabIndex:0
            }
        },
        methods:{
            disperse_tabbar(index){
                this.disperse_tabIndex = index
            }
        },
        components:{
            Goods
        }
    }    
</script>
<style>
.disperse_tab{
    margin-top:15px;
    border-bottom:1px solid #ededed;  
    display: flex;
}
.disperse_tab li{
    padding-bottom:9px;
    
    margin: 0 13px;
    margin-bottom:-1px;
    
}
.disperse_tab_li{
    border-bottom:2px solid red; 
    color:red 
}
.disperse_tab span{
    font-size: 14px;
}
</style>


